<template>
  <div class="card">
    <div class="card-header">ChildA  [부모와 자식간의 관계가 아닌 포함 관계]</div>
    <div class="card-body">
        <div>
            <p>data1 : {{message.data1.value}}</p>
            <p>data2.name1 : {{message.data2.value.name1}}</p>
            <p>data2.name1 : {{message.data2.value.name2}}</p>
            <p>data3.name1 : {{message.data3.name3}}</p>
            <p>data3.name1 : {{message.data3.name4}}</p>
        </div>
        <button @click="changeData" class="mt-2 btn btn-info btn-sm">데이터 변경</button>
    </div>
  </div>
</template>

<script setup>
import { inject } from 'vue';

const message = inject("message");

function changeData(){
    message.data1.value += "1";
    message.data2.value.name1 += "2";
    message.data2.value.name2 += "3";
    message.data3.name3 += "4";
    message.data3.name4 += "5";
}
</script>

